
.addItem{
	float:right;
	font-size:30px;
	color:#fff;
	position:relative;
	display: none;
}
.addItem p{
	position:absolute;
	right:0;
	font-size:18px;
	width:175px;
	margin-top: 7px;
	display:none;
}
.addItem p span{
	display:inline-block;
	line-height:30px;
	text-align:center;
	padding:0 5px;
	color:#fff;
	border-radius:10px;
	background: #0e76ac;
	margin-left:5px;
}
.addItem>span{
	display:inline-block;
	line-height:30px;
	text-align:center;
	width:30px;
	height:30px;
	border-radius:50%;
	background: #0e76ac;
}
.addItem:hover p{display:block;
}




